.board-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: #f4f5f8;
}

.ticket-orientation {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 40px;
  padding: 20px;
  flex-grow: 1;
  max-width: 100vw;
  overflow-x: scroll;
}
